<template>
  <v-card flat color="#ffffff" class="my-4" style="height:100vh;">
    <div v-if="timeBooking" style="position: absolute;top: 0; right: 0; max-width: 500px">
      <v-alert
        :value="true"
        color="info"
        icon="info"
        outline
        class="my-0 px-2 py-2"
      >
        - T khai  ng k lch hn online.<br>
        - Ni tip nhn h s: {{agencyTiepNhan ? agencyTiepNhan['name'] : ''}}.<br>
        - Thi gian tip nhn h s: {{timeBooking}} ngy {{checkinDate}}.<br>
        - Qua thi gian xp hng trn nu cha n lm th tc  ngh ng k xp hng li.
      </v-alert>
    </div>
    
    <v-container align-center row wrap class="px-0 pt-0">
      <v-flex xs12 style="border: 1px solid #0072bc; border-radius: 3px">
        <v-layout row wrap>
          <h2 class="flex pt-2 text-xs-center">
            <span style="color:#065694">THNG TIN T KHAI </span>
          </h2>
          <v-flex xs12 sm12 class="pb-3">
            <div class="px-3 mt-3" style="font-size:16px">
              <p class="mb-2 text-bold">Tn t khai: <span class="ml-2 primary--text">{{fileTemplateSelected.templateName}}</span></p>
              <p class="mb-2 text-bold">M t khai: <span class="ml-2 primary--text">{{eformDetail.eFormNo }}</span></p>
              <p class="mb-2 text-bold">M b mt tra cu t khai: <span class="ml-2 primary--text">{{eformDetail.secret}}</span></p>
              <p class="mb-2 text-bold" v-if="agencyTiepNhan">Ni np t khai: <span class="ml-2 primary--text">{{agencyTiepNhan ? agencyTiepNhan['name'] : ''}}</span></p>
              
              <span class="mr-2"><v-icon color="warning">warning</v-icon></span>
              <span class="warning--text" style="font-size: 1.2em">Vui lng ghi li M t khai, M b mt  tra cu, in li t khai khi cn thit!</span>
              <div class="mt-3">
                <v-btn color="success" class="white--text" 
                  @click="previewEform"
                  :loading="dialogPDFLoading"
                  :disabled="dialogPDFLoading"
                >
                  <v-icon>visibility</v-icon> &nbsp;
                  Xem trc
                </v-btn>
                <v-btn color="primary" class="ml-3 white--text"
                @click="downloadEform"
                  :loading="dialogPDFLoading"
                  :disabled="dialogPDFLoading"
                >
                  <v-icon>cloud_download</v-icon> &nbsp;
                  Ti xung
                </v-btn>
              </div>
              <div v-if="!agencyTiepNhan || (agencyTiepNhan && agencyTiepNhan['code'] === 'CLS')">
                <p class="mt-4">
                  -   hon tt th tc,  ngh mang T khai ny cng cc giy t theo quy nh n Cc Lnh s 
                  hoc S Ngoi v Thnh ph H Ch Minh hoc Ngoi v a phng hoc 
                  C quan i din  xin chng nhn/ hp php ha lnh s.
                </p>
                <p>
                  -  T khai ny ch c gi tr hiu lc trong vng 10 ngy lm vic. Qu thi hn trn nu cha np t khai  ngh k khai t khai mi.
                </p>
                <p>
                  -  Th tc cp, gia hn h chiu ngoi giao, h chiu cng v, xin cng hm th thc quy s 3.
                </p>
                <p>
                  -  Th tc hp php ha lnh s, chng nhn lnh s quy s 7.
                </p>
                <p v-if="timeBooking">
                  -  B phn mt ca - Cc Lnh s xc nhn lch hn np h s Qu khch vo {{' '+ timeBooking + ' ngy '+ checkinDate + ' '}} vui lng n trc gi hn 10' v sp xp h s ng,  theo quy nh. Qu thi gian hn tr trn, Qu khch vui lng ng k xp hng li.
                </p>
              </div>
              <div v-else>
                <p class="mt-4">
                  -   hon tt th tc,  ngh mang T khai ny cng cc giy t theo quy nh n Cc Lnh s 
                  hoc S Ngoi v Thnh ph H Ch Minh hoc Ngoi v a phng hoc 
                  C quan i din  xin chng nhn/ hp php ha lnh s.
                </p>
                <p>
                  -  T khai ny ch c gi tr hiu lc trong vng 10 ngy lm vic. Qu thi hn trn nu cha np t khai  ngh k khai t khai mi.
                </p>
                <p v-if="timeBooking">
                  -  B phn mt ca - S ngoi v Tp. H Ch Minh xc nhn lch hn np h s Qu khch vo {{' '+ timeBooking + ' ngy '+ checkinDate + ' '}} vui lng n trc gi hn 10' v sp xp h s ng,  theo quy nh. Qu thi gian hn tr trn, Qu khch vui lng ng k xp hng li.
                </p>
              </div>
            </div>
          </v-flex>
          <v-flex xs12 class="text-xs-right mt-3 mb-2">
            <v-btn color="primary" class="mx-3 white--text" @click="goBack">
              <v-icon>reply</v-icon> &nbsp;
              Quay li
            </v-btn>
          </v-flex>
        </v-layout>
      </v-flex>
    <!--  -->
    </v-container>
    
    <v-dialog fullscreen v-model="dialogPDF" transition="fade-transition">
      <v-card>
        <v-toolbar flat dark color="primary">
          <v-toolbar-title>T khai trc tuyn</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn title="Thot" icon dark @click.native="dialogPDF = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text class="px-0 py-0">
          <div v-if="dialogPDFLoading" style="
              min-height: 700px;
              text-align: center;
              margin: auto;
              padding: 25%;
          ">
            <v-progress-circular
              :size="100"
              :width="1"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </div>
          <iframe v-show="!dialogPDFLoading" id="dialogPDFPreview" src="" type="application/pdf" width="100%" height="100%" style="overflow: auto;min-height: 600px;" frameborder="0">
          </iframe>
        </v-card-text>
        
        <v-card-actions class="mx-2">
          <v-spacer></v-spacer>
          <v-btn color="primary" class="white--text mr-2" @click.native="printEform">
            <v-icon>print</v-icon> &nbsp;
            In t khai
          </v-btn>
          <v-btn color="red" class="white--text" @click.native="() => dialogPDF = false">
            <v-icon>clear</v-icon> &nbsp;
            Thot
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>

import Vue from 'vue'
import $ from 'jquery'
import toastr from 'toastr'
import axios from 'axios'
import support from '../../store/support.json'
Vue.use(toastr)
export default {
  props: ['index'],
  components: {
  },
  data: () => ({
    serviceInfoList: [],
    eformNoSearch: '',
    formScriptEform: '',
    dialogPDF: false,
    dialogPDFLoading: false,
    timeBooking: '',
    checkinDate: '',
    agencyItems: [
      {name: 'Cc lnh s - 40 Trn Ph, Ba nh, H Ni', value: '124302', code: 'CLS', serverNo: 'SERVER_CLS'}
    ],
    agencyTiepNhan: ''
  }),
  computed: {
    serviceinfoSelected () {
      return this.$store.getters.getServiceinfoSelected
    },
    fileTemplateSelected () {
      return this.$store.getters.getFileTemplateSelected
    },
    eformDetail () {
      return this.$store.getters.getEformDetail
    },
    serverCode () {
      return this.$store.getters.getServerNo
    }
  },
  created () {
    var vm = this
    vm.$nextTick(function () {
      try {
        vm.agencyItems = agencyItems /**config fragment*/
      } catch (error) {
      }
      let current = vm.$router.history.current
      let currentQuery = current.query
      if (!vm.eformDetail.secret && !vm.eformDetail.eFormNo) {
        vm.goBack()
      }
      if (String(vm.index) === '1') {
        vm.getDetailBooking()
      }
      try {
        vm.agencyTiepNhan = vm.agencyItems.filter(function (item) {
          return item.code === vm.eformDetail.govAgencyCode
        })[0]
      } catch (error) {
      }
      
    })
  },
  updated () {
    var vm = this
    vm.$nextTick(function () {
    })
  },
  watch: {
    '$route': function (newRoute, oldRoute) {
      let vm = this
      let currentParams = newRoute.params
      let currentQuery = newRoute.query
    }
  },
  methods: {
    downloadEform () {
      let vm = this
      vm.dialogPDFLoading = true
      vm.$store.dispatch('downloadEform', vm.eformDetail).then(function (result) {
        vm.dialogPDFLoading = false
      }).catch(function (reject) {
        vm.dialogPDFLoading = false
      })
    },
    previewEform () {
      let vm = this
      vm.dialogPDFLoading = true
      vm.$store.dispatch('previewEform', vm.eformDetail).then(function (result) {
        vm.dialogPDFLoading = false
        vm.dialogPDF = true
        document.getElementById('dialogPDFPreview').src = result
      }).catch(function (reject) {
        vm.dialogPDFLoading = false
      })
    },
    printEform () {
      try {
        document.getElementById("dialogPDFPreview").contentWindow.print()
      } catch (error) {
      }
    },
    getDetailBooking () {
      let vm = this
      let serverNoInfo = this.$store.getters.getServerNo
      let serverNo = serverNoInfo ? serverNoInfo : 'SERVER_CLS'
      let filter = {
        codeNumber: vm.eformDetail.eFormNo ? vm.eformDetail.eFormNo : '',
        serverCode: serverNo
      }
      vm.$store.dispatch('getDetailBookingProxy', filter).then(function (result) {
        vm.timeBooking = result.bookingInTime ? result.bookingInTime : ''
        vm.checkinDate = vm.parseDate(result.checkinDate)
      }).catch(function (reject) {
      })
    },
    parseDate (value) {
      if (!value) return ''
      let date = ''
      if (!isNaN(Number(value))) {
        date = new Date(Number(value))
      } else {
        date = new Date(value)
      }
      if (date) {
        return `${date.getDate().toString().padStart(2, '0')}/${(date.getMonth() + 1).toString().padStart(2, '0')}/${date.getFullYear()}`
      } else {
        return ''
      }
    },
    goBack () {
      let vm = this
      vm.$router.push({
        path: '/',
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
        }
      })
    }
  }
}
</script>
